// your global classes and style-settings
.iOS {
  cursor: pointer; }
html {
  box-sizing: border-box; }
* {
  box-sizing: inherit; }
*::before,
*::after {
  box-sizing: inherit; }
body {
  min-height: 100vh;

  position: relative;

  font-family: 'Barlow', Arial;
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  text-transform: uppercase; }

.body--noscroll {
  max-height: 100vh;
  height: 100%;
  overflow: hidden; }

.wrap {
  width: 100%;
  max-width: 1080px;

  margin-left: auto;
  margin-right: auto;
  padding-left: 25px;
  padding-right: 25px; }
